import { style, animate, transition, trigger, state } from '@angular/animations';

export const slideUp = [
    style({ opacity: 0, transform: 'translateY(50px)' }),
    animate(500, style({ opacity: 1, transform: 'translateY(0px)' }))
];

export const slideDown = [
    style({ opacity: 0, transform: 'translateY(-50px)' }),
    animate(500, style({ opacity: 1, transform: 'translateY(0px)' }))
];

export const fadeIn = [
    style({ opacity: 0 }),
    animate(500, style({ opacity: 1 }))
];

export const navbarTransition = trigger('navbarAnimations', [
    state('in', style({
        opacity: 1,
        transform: 'translateX(0px)'
    })),
    state('out', style({
        opacity: 0,
        transform: 'translateX(-700px)'
    })),
    transition('in => out', [animate(200)]),
    transition('out => in', [animate(200)]),
]);
